<template>
  <div class="search">
    <!-- 头部搜索栏 -->
    <div class="title" v-if="topShow">
      <van-search
        v-model="value"
        show-action
        placeholder="请输入搜索关键词"
        @search="onSearch"
        @input="asonSearch"
        :class="['bg-gradient-to-r from-lightBlue-500 to-blue-400 rounded-b-lg', {'animate__animated animate__flipInX ': controlAnimate}]"
      >
        <template #action>
          <div @click="$router.push('/index')" class="font-mono text-coolGray-200 font-bold">取消</div>
        </template>
      </van-search>
    </div>
    <!-- 历史搜索记录 -->
    <div v-if="cellShow" class="mt-6 border-2 rounded-xl">
      <van-cell title="历史区域" class="py-3 font-serif text-xl text-lightBlue-600">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #right-icon>
            <van-icon name="delete" @click="clear" />
          </template>
        </van-cell>
      <div
        v-for="(item, index) in historyList"
        :key="index"
        @click="$router.push('/searchResult/?requies=' + item)"
        class="mt-1 border-b-2 w-11/12 mx-auto font-mono shadow-md animate-pulse"
      >
        <van-cell :title="historyList[index]" icon="search">
        </van-cell>
      </div>
    </div>
    <!-- 联想区域 -->
    <div v-if="association" class="mt-6 border-2 rounded-xl">
        <van-cell title="联想区域" class="py-3 font-serif text-xl text-lightBlue-600">
        </van-cell>
      <div
        v-for="(item, index) in asscontion"
        :key="index"
        @click="$router.push('/searchResult/?requies=' + item)"
        class="mt-1 border-b-2 w-11/12 mx-auto font-mono shadow-md animate-pulse"
      >
        <van-cell icon="search">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #default>{{ asscontion[index] }}</template>
        </van-cell>
      </div>
    </div>
  </div>
</template>
<script>
import {
  hotSearchApi,
  getSearchApi,
  associationApi,
  removeApi
} from '../../../api/indexApi.js'
export default {
  data () {
    return {
      value: '',
      indexList: [],
      // 控制历史搜索显示隐藏
      cellShow: false,
      // 保存搜索历史数据
      historyList: [],
      // 控制头部栏的显示和隐藏
      topShow: true,
      // 控制搜索结果栏的显示和隐藏
      searchShow: false,
      // 搜索结果
      searchList: [],
      // 联想区域显示和隐藏
      association: false,
      // 联想数据
      asscontion: [],
      controlAnimate: true
    }
  },
  // 获取热门搜索数据
  async created () {
    this.association = false
    this.cellShow = true
    const res = await hotSearchApi({
      q: this.value
    })
    console.log(res)
    this.historyList = res.data.keywords
    // 去重
    this.historyList = [...new Set(this.historyList)]
  },
  mounted () {
    setTimeout(() => {
      this.controlAnimate = false
    }, 900)
  },
  methods: {
    // 联想获取数据
    async asonSearch () {
      this.cellShow = false
      this.association = true
      if (this.value) {
        const res = await associationApi({
          q: this.value
        })
        this.asscontion = res.data.options
      } else {
        this.asscontion = []
        this.cellShow = true
        this.association = false
      }
      this.asscontion.push(this.value)
      // 去重
      this.asscontion = [...new Set(this.asscontion)]
      // 限长： 搜索历史中最多保存 6 条数据
      this.asscontion.splice(6)
    },
    // 回车搜索数据
    async onSearch () {
      const res = await getSearchApi({
        q: this.value
      })
      this.searchList = res.data.results
      this.historyList.push(this.value)
      if (this.searchList) {
        this.$router.push('/searchResult/?requies=' + this.value)
      }
      this.value = ''
    },
    clear () {
      // 访问是否清空
      this.$dialog
        .confirm({
          title: '提示',
          message: '是否清空历史数据'
        })
        .then(() => {
          this.historyList.splice(0, this.historyList.length)
          removeApi()
          this.$toast.success('搜索历史清除完成')
        })
        .catch(() => {})
    }
  }
}
</script>
